  position: absolute;
}

.mobile-menu__label .hamburger:before, .mobile-menu__label .hamburger .center, .mobile-menu__label .hamburger:after {
  --tx: 0;
  --rt: 0deg;
  content: "";
  width: 100%;
  height: var(--weight);
  transform-origin: 100%;
  transition: var(--bounce) .1s;
  background: #db6300;
  border-radius: .3em;
  position: absolute;
}

.mobile-menu__label .hamburger .center {
  transition: var(--bounce), width var(--ease);
  transition-delay: .3s;
  right: 0;
}

.mobile-menu__label .hamburger:before {
  transform: translate(var(--tx), 100%) rotate(var(--rt) );
